.descriptionbox {
  margin: 12px 170px;
}
.descriptionbox-navigator {
  display: flex;
}
.descriptionbox-navbox {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 600;
  width: 141px;
  height: 50px;
  border: 1px solid #d0d0d0;
}
.descriptionbox-navbox.fade {
  background: #fbfbfb;
  color: #555;
}
.descriptionbox-description {
  display: flex;
  flex-direction: column;
  gap: 25px;
  border: 1px solid #d0d0d0;
  padding: 48px;
  padding-bottom: 40px;
  font-size: 15px;
}
@media (max-width: 1280px) {
  .descriptionbox {
    margin: 60px;
  }
}

@media (max-width: 800px) {
  .descriptionbox {
    margin: 60px 20px;
  }
  .descriptionbox-description {
    font-size: 14px;
  }
}
