.loginsignup {
  width: 100%;
  height: 100vh;
  background: #fce3fe;
  padding-top: 50px;
}
.loginsignup-container {
  width: 500px;
  height: 520px;
  background: #fff;
  margin: auto;
  padding: 20px 60px;
}
.loginsignup-container h1 {
  margin: 0 0px;
}
.loginsignup-fields {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 30px;
}
.loginsignup-fields input {
  height: 52px;
  width: 90%;
  padding-left: 20px;
  border-radius: 1px solid #c9c9c9;
  color: #5c5c5c;
  font-size: 18px;
  outline: none;
}
.loginsignup-container button {
  width: 95%;
  height: 52px;
  color: #fff;
  background: crimson;
  margin-top: 30px;
  border: none;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
  font-family: "poppins";
}
.loginsignup-login {
  margin-top: 30px;
  color: #5c5c5c;
  font-size: 18px;
  font-weight: 500;
}
.loginsignup-login span {
  color: crimson;
  font-weight: 600;
}
.loginsignup-agree {
  display: flex;
  align-items: center;
  margin-top: 25px;
  gap: 20px;
  color: #5c5c5c;
  font-size: 18px;
  font-weight: 500;
}
@media (max-width: 1280px) {
  .loginsignup {
    padding-top: 50px;
  }
  .loginsignup-container {
    width: auto;
    max-width: 500px;
    max-height: 500px;
  }
  .loginsignup-container h1 {
    margin: 10px 0;
  }
  .loginsignup-fields {
    gap: 20px;
    margin-top: 20px;
  }
  .loginsignup-fields input {
    height: 65px;
    width: 93%;
  }
  .loginsignup-container button {
    width: 100%;
  }
  .loginsignup-login {
    font-size: 16px;
  }
  .loginsignup-agree {
    font-size: 14px;
    gap: 10px;
  }
}
@media (max-width: 800px) {
  .loginsignup-container {
    padding: 20px 30px;
  }
  .loginsignup-container h1 {
    font-size: 24px;
  }
  .loginsignup-fields {
    margin-top: 20px;
  }
  .loginsignup-fields input {
    height: 50px;
  }
  .loginsignup-container button {
    height: 50px;
    font-size: 16px;
  }
}
